}
.me {
  background-color: rgba(38, 37, 37, 0.299);
}

.focus:focus {
  border: 1px solid #4eb5d1;
}

p {
  margin: 0px;
  padding: 0px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
