.table-loader-component {
  width: 100%;
}
.table-loader-component table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
}

.wrapper-loader {
  /* margin: auto; */
  /* display: flex; */
  /* width: 100%;
  border-radius: 6px;
  border: 1px solid lightgray;
  box-shadow: 0px 0px 18px -1px #2e2e2e;
  flex-wrap: wrap; */
  /* height: 7rem; */
  /* background-color: green; */
  height: 389px;
  width: 100%;
  border-collapse: collapse;
}

.wrapper-loader .row {
  padding: 2rem 1rem;
  display: flex;
  align-items: center;
}

.wrapper-loader .row .prod-img {
  margin-right: 1rem;
  width: 3.2rem;
  height: 3.2rem;
  border-radius: 50%;
}

.wrapper-loader .others {
  width: 70%;
  border-radius: 0.8rem;
  margin-left: 15px;
  /* margin: 2rem; */
}

.skeleton-loader:empty {
  display: block;
  background-color: #919497df;
  animation-name: skeletonAnimation;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

.skeleton-loader:empty.others {
  height: 389px;
  width: 365px;
}

@keyframes skeletonAnimation {
  0% {
    opacity: 0.3;
  }
  50% {
    opacity: 0.2;
  }
  100% {
    opacity: 0.3;
  }
}
